import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/imprint',
    name: 'imprint',
    component: () => import(/* webpackChunkName: "sidebar" */ '../views/imprint.vue'),
  },
  {
    path: '/agb',
    name: 'agb',
    component: () => import(/* webpackChunkName: "sidebar" */ '../views/agb.vue'),
  },
  {
    path: '/datersuts',
    name: 'datersuts',
    component: () => import(/* webpackChunkName: "sidebar" */ '../views/datersuts.vue'),
  },
  {
    path: '/sidebar',
    name: 'sidebar',
    // route level code-splitting
    // this generates a separate chunk (sidebar.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "sidebar" */ '../views/sidebarMobile.vue'),
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue'),
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

export default router
