import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import VueAxios from 'vue-axios'

Vue.config.productionTip = false
Vue.use(VueAxios, axios)

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    category: [
      {
        title: 'Burger',
        id: 1,
        price: '560',
        image: './assets/burger.webp',
      },
      {
        title: 'pasta',
        id: 2,
        price: '200',
        image: '../assets/pasta.jpg',
      },
      {
        title: 'salad',
        id: 3,
        price: '500',
        image: '../assets/salad.jpg',
      },
      {
        title: 'pizza',
        id: 4,
        price: '670',
        image: '../assets/burger.webp',
      },

    ],
    cart: [],
  },
  mutations: {
    addToCart: (state, productId) => {
      // find the product in the products list
      const cartProduct = state.cart.find((product) => product.id === productId)
      var product = null
      for (const cate of state.category) {
        if (cate != null && cate.id === productId) {
          product = cate
          if (cartProduct) {
            cartProduct.quantity++
          } else {
            state.cart.push({
              ...product,
              stock: product.quantity,
              quantity: 1,
            })
          }
        }
      }
    },
    removeFromCart: (state, productId) => {
      // find the product in the cart list
      const cartProduct = state.cart.find((product) => product.id === productId)

      cartProduct.quantity--
    },
    deleteFromCart: (state, productId) => {
      // find the product index in the cart list
      const cartProductIndex = state.cart.findIndex((product) => product.id === productId)
      // remove the product from the cart
      state.cart.splice(cartProductIndex, 1)
    },
  },
  actions: {
    addToCart: ({ commit }, productId) => {
      commit('addToCart', productId)
    },
    removeFromCart: ({ commit }, productId) => {
      commit('removeFromCart', productId)
    },
    deleteFromCart: ({ commit }, productId) => {
      commit('deleteFromCart', productId)
    },
  },
  modules: {
  },
  getters: {
    category: (state) => state.category,
    cart: (state) => state.cart,
    storage: (state) => {
      return state.storage
    },
    cartSize: (state) => {
      return state.cart.length
    },
    cartTotalAmount: (state) => {
      return state.cart.reduce((total, product) => {
        return total + (product.price * product.quantity)
      }, 0)
    },
  },
})
